import Vue from 'vue'
import Vuex from 'vuex'
import user from './plugins/activeUser'
Vue.use(Vuex)

export default new Vuex.Store({
    modules:{
        user
    },
    state: {
        defaultColor:{
            headerColor : "#a6dcef",
            sideBarColor : "#fff",
            sideBarButtonActiveColor : "#f2aaaa",
            success : "#66BB6A",
            accent: "#3F51B5",
            purple_accent : "#EA80FC",
            default : "#a6dcef",
            danger:  "#c70039",
            info : "#CFD8DC",
            primary: "#FF5722",
        },
        Sidebar_drawer: null,
        Customizer_drawer: false,
        SidebarColor: 'red',
        SidebarBg: '',
        locale:"tr"
      },
      getters:{
          defaultColor(state){
              return state.defaultColor;
          }
      },
    mutations: {
        SET_SIDEBAR_DRAWER (state, payload) {
            state.Sidebar_drawer = payload
        },
        SET_CUSTOMIZER_DRAWER (state, payload) {
            state.Customizer_drawer = payload
        },
        SET_SIDEBAR_COLOR (state, payload) {
            state.SidebarColor = payload
        }, 
    },
    actions: {

    }
})