import api from './api'
export default {
    state:{
        activeUser :{
            name:"Gürhan",
            lastname : "Arslan",
            status:"Müdür",
            token:""
        },
        schoolData:{
            "headerColor": null, "sideBarColor": null, "successColor": null, "sideBarButtonActiveColor":null, "primaryColor": null, image:null, name:null
        },
        token:""
    },
    getters:{
        getActiveUser:(state)=>{
            return state.activeUser;
        },
        getSchoolData:(state)=>{
            return state.schoolData;
        }
    },
    mutations:{
        setActiveUser(state,payload){
            payload.image = payload.image.replace('public/', api.BASE_API_URL)
            localStorage.setItem('USER_',JSON.stringify({username:payload.username,password:payload.password}))
            state.activeUser = payload
        },
        setToken(state,payload){
            state.token = payload
            localStorage.setItem('KEY',payload)
        },
        setSchoolData(state,payload){
            payload.image = payload.image.replace('public/', api.BASE_API_URL)
            state.schoolData = payload
            localStorage.setItem('SCHOOL_',JSON.stringify(payload))
        }
    },
    actions:{}
}