<template>
   <v-snackbar
      v-model="undo"
      bottom
      :vertical="true"
      :timeout="24000"
    >
      {{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn
          dark
          text
          v-bind="attrs"
         @click="handleClick"
        >
         Geri Al
        </v-btn>
      </template>
    </v-snackbar>
</template>

<script>
export default {
props:{
    undo:{
        default: false,
        required: true,
        type: Boolean
    },
    text:{
        default : 'İşleminiz başarıyla gerçekleşti. Yaptığınız işlemi geri alabilirsiniz.',
        required:false,
        type:String

    }
},
methods:{
        handleClick(evt) {
    this.$emit('undo', evt)

    },
},
watch:{
  undo(data){
    if(data){
      setTimeout(() => {
        this.$emit('finished',false)
      }, 4000);
    }
  }
}
}
</script>

