<template>
  <v-row justify="center">  
  <v-dialog
      v-model="dialog"
      max-width="390"
    >
      <v-card>
        <v-card-title class="headline">{{title}}</v-card-title>

        <v-card-text>
          {{text}}
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
          v-if="!hide"
            color="green darken-1"
            text
            @click="removeClick"
          >
            İptal Et
          </v-btn>

          <v-btn
            color="green darken-1"
            text
            @click="handleClick"
          >
            Onayla
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
     <snack-bar :snackbar="snackbar"></snack-bar>
  </v-row>
</template>

<script>
  export default {
    props:{
        dialog:{
            default:false,
            required:true,
            type:Boolean
        },
         hide:{
            default:false,
            required:false,
            type:Boolean
        },
        text:{
            default: "İşleminizi onaylıyor musunuz?",
            required:false,
            type:String
        },
        title:{
            default: "Onaylama",
            required:false,
            type:String
        }
    },
    data(){
        return{
            snackbar:false
        }
    },
    watch:{
      dialog(data){
        this.$emit('dialog',data)
      }
    },
    methods: {
    handleClick(evt) {
    this.$emit('confirm', evt)
        
    },
    removeClick(evt) {
    this.$emit('cancel', evt)
    
    }
  }
  }
</script>