  init: (user,token)=>{
    var myHeaders = new Headers();
myHeaders.append("token", token);

var requestOptions = {
  method: 'GET',
  headers: myHeaders,
  redirect: 'follow'
};

return fetch(`${API_URL_1}/user?filter=username:"${user.username}",password="${user.password}"`, requestOptions)
  .then(response => response.json())
  .then(result => {
    let res = auth(result)
    if(res=='success'){
      if(result.count==0){
        router.push({name:'login'})
        localStorage.clear()
        return {count:0}
      }
      else{
        return result
      }
    }
    else{
      return {count:0}
    }
  })
  .catch(error => console.log('error', error));
  }
