function logout() {
    localStorage.clear()
    router.push('/login')
}
async function login(user) {
  let result = await api.login(user)
  if(result.status=='error'){
      return 'fail'
  }
  else{
    store.commit('setToken',result.token)
    store.commit('setSchoolData',result.schoolData)
    store.commit('setActiveUser',{...result.data,token:result.token})
    return 'success'
  }

}

function dataFormatterNaApiWithTime (data){

  let [newDate,newTime] = data.split('T')
  newTime = newTime.split('.')[0]
  let [year,month,date] = newDate.split('-')

  return date+'.'+month+'.'+year+' '+newTime
}

function dataFormatterNaApi(data){

  let newDate = data.substr(0,10)
  let [year,month,date] = newDate.split('-')

  return year+'-'+month+'-'+date
}