import api from './api'
import router from '../router'
export default  ()=>{
   if(localStorage.getItem('USER_')&&localStorage.getItem('KEY')&&localStorage.getItem('SCHOOL_'))
{
   return api.init(JSON.parse(localStorage.getItem('USER_')),localStorage.getItem('KEY')).then(res=>{
      if(res.count==0){
         let promise = new Promise(function(resolve,) {
            localStorage.clear()
            router.push({name:"login"})
            resolve({status:'fail'})
          });
          return promise;
      }
      else{
         return {status:'success', data:res}
      }
   })
}
else{
   let promise = new Promise(function(resolve) {
      localStorage.clear()
      router.push({name:"login"})
      resolve({status:'fail'})
    });
    return promise;
}
  
}