import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

const beforeEnter = (to, from, next) => {
    let key =  localStorage.getItem('USER_')
    if(key===null){next({name: "login"})}
    else next()

  }
  export default new Router({
      mode: 'hash',
      base: process.env.BASE_URL,
      routes: [
          {
              path: '/',
              redirect: 'dashboard/basic-dashboard',

              component: () => import('@/layouts/Layout'),
              children: [
                  // Components
                  {
                      name: 'Alerts',
                      path: 'pages/class-list',
                      beforeEnter:beforeEnter,
                      component: () => import('@/views/pages/class-list'),
                  },

                  {
                      name: 'Profile',
                      beforeEnter:beforeEnter,
                      path: 'pages/insert-teacher',
                      component: () => import('@/views/pages/insert-teacher'),
                  },
                  {
                      name: 'Profile',
                      beforeEnter:beforeEnter,
                      path: 'pages/insert-parent',
                      component: () => import('@/views/pages/insert-parent'),
                  },
                  {
                      name: 'TableSimple',
                      beforeEnter:beforeEnter,
                      path: 'pages/student-list',
                      component: () => import('@/views/pages/student-list'),
                  },
                  {
                      name: 'TableSimple',
                      beforeEnter:beforeEnter,
                      path: 'pages/event-list',
                      component: () => import('@/views/pages/event-list'),
                  },
                  {
                      name: 'TableSimple',
                      beforeEnter:beforeEnter,
                      path: 'pages/annoucment-list',
                      component: () => import('@/views/pages/annoucment-list'),
                  },
                  {
                      name: 'TableSimple',
                      path: 'pages/lesson-list',
                      beforeEnter:beforeEnter,
                      component: () => import('@/views/pages/lesson-list'),
                  },
                  {
                    name: 'TableSimple',
                    path: 'pages/food-list',
                    beforeEnter:beforeEnter,
                    component: () => import('@/views/pages/food-list'),
                },
                {
                    name: 'TableSimple',
                    path: 'pages/monthly-news',
                    beforeEnter:beforeEnter,
                    component: () => import('@/views/pages/monthly-news'),
                },
                {
                    name: 'TableSimple',
                    path: 'pages/lessons',
                    beforeEnter:beforeEnter,
                    component: () => import('@/views/pages/lessons'),
                },
                {
                    name: 'TableSimple',
                    path: 'pages/messages',
                    beforeEnter:beforeEnter,
                    component: () => import('@/views/pages/messages'),
                },
                {
                    name: 'TableSimple',
                    path: 'pages/settings',
                    beforeEnter:beforeEnter,
                    component: () => import('@/views/pages/settings'),
                },
                {
                    name: 'TableSimple',
                    path: 'pages/host',
                    beforeEnter:beforeEnter,
                    component: () => import('@/views/pages/host'),
                },
                {
                    name: 'TableSimple',
                    path: 'pages/list-host',
                    beforeEnter:beforeEnter,
                    component: () => import('@/views/pages/list-host'),
                },
                {
                    name: 'TableSimple',
                    path: 'pages/profile',
                    beforeEnter:beforeEnter,
                    component: () => import('@/views/pages/profile'),
                },
                {
                    name: 'TableSimple',
                    path: 'pages/feed-back',
                    beforeEnter:beforeEnter,
                    component: () => import('@/views/pages/feedback'),
                },

                  {
                      name: 'Dashboard',
                      path: 'dashboard/basic-dashboard',
                      beforeEnter:beforeEnter,
                      component: () => import('@/views/dashboard/BasicDashboard'),
                  },


              ]
          },
          {
              path: '/login',
              name:"login",
              component: () => import('@/views/pages/login'),
          },
          {
              path:'*',
              redirect:'/login',
              component: () => import('@/views/pages/login'),

          }
      ],
  })