import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify'
import router from './router'
import store from './store'
import './plugins/base'
import VueI18n from 'vue-i18n'
import settings from './plugins/settings'
import err from './plugins/err'
import api from './plugins/api'
import snackBar from './components/snackbars/snackbar.vue'
import undo from './components/snackbars/undo.vue'
import confirm from './components/snackbars/confirm.vue'
import loading from './components/snackbars/loading.vue'

import 'element-ui/lib/theme-chalk/index.css'
import {Upload} from 'element-ui'
Vue.use(Upload)
Vue.use(VueI18n)

Vue.component('snackBar',snackBar)
Vue.component('undo',undo)
Vue.component('confirm',confirm)
Vue.component('loading',loading)
Vue.prototype.$settings = settings
Vue.config.BASE_URL = 'wwww.eralpsoftware.com'
Vue.prototype.$err = err
Vue.prototype.$api = api

const i18n = new VueI18n({
    locale : store.state.locale,
    fallbackLocale:'tr',
    messages:{
        tr : require('./locales/tr.json')
    }
})


Vue.config.productionTip = false

new Vue({
    vuetify,
    i18n,
    store,
    router,
    render: h => h(App)
}).$mount('#app')
