<template>
  <v-app id="materialpro" :class="`${!$vuetify.breakpoint.smAndDown ? 'full-sidebar' : 'mini-sidebar'}`">
     <loading v-if="loading"/>
        <router-view v-else  />
  </v-app>

</template>

<script>
import init from './plugins/init'
export default {
  name: 'App',
  data:()=>({
    loading:true
  }),
  mounted(){
  init()
  // console.log(res);
  // if(res.status=='fail'){
  //   this.loading=false
  // }
   .then(res=>{
    if(res.status=='success'){
       this.$store.commit('setActiveUser',res.data.body[0])
     this.$store.commit('setSchoolData',JSON.parse(localStorage.getItem('SCHOOL_')))
     this.$store.commit('setToken',localStorage.getItem('KEY'))
      this.loading=false
    }
    else{
      this.loading=false
    }
   })
  }
};
</script>

