<template>
   <v-snackbar
      v-model="snackbar"
      top
    >
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          dark
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
</template>

<script>
export default {
props:{
    snackbar:{
        default: false,
        required: true,
        type: Boolean
    },
    text:{
        default : 'İşleminiz başarıyla gerçekleşti.',
        required:false,
        type:String

    }
},
watch:{
  snackbar(data){
    if(data){
      setTimeout(() => {
        this.$emit('finished',false)
      }, 4000);
    }
  }
}
}
</script>

